.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: rgba(245, 245, 245, 0.781);
  color: black;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: small;
}
@media screen and (max-width: 550px) {
